
.search-box {
        margin-top: 15px;
        margin-bottom: 15px;
}
.search-field {
        display: inline-block;
        background: none;
        border: none;
        border-bottom: 1px solid rgba(200, 200, 200, .75);
}
.search-button {
        width: 50px;
}
.glass {
        border-radius: 5px;
        background-color: rgba(0, 0, 0, .5);
        color: rgba(200, 200, 200, .75);
}
input[type="text"].search-field {
        color: #ffffff;
}
input[type="text"].search-field:focus {
        color: #ffffff;
        outline: none;
}
::-webkit-input-placeholder {
        /* Chrome/Opera/Safari */
        color: #ffffff;
}
::-moz-placeholder {
        /* Firefox 19+ */
        color: #ffffff;
}
:-ms-input-placeholder {
        /* IE 10+ */
        color: #ffffff;
}
:-moz-placeholder {
        /* Firefox 18- */
        color: #ffffff;
}
.song-box {
        margin-bottom: 25px;
}
.song-pic {
        margin-top: 5px;
}

    /* .song-thumb{
    margin: 15px 15px 60px 15px;
} */
.list-button {
        background: none;
        background-color: rgba(200, 200, 200, 1);
        color: #000000;
        border: none;
        border-radius: 50%;
        height: 30px;
        width: 30px;
}
.audio-preview {
        width: 75%;
}
.l-margin-5 {
        margin-left: 5px;
}



